<template>
    <div>
        <header :class="scrollActive ? 'sticky' : ''" id="sticky-header" v-if="$route.name !== 'registerPage'">
            <nav class="navbar navbar-expand-xl navbar-expand-lg navbar-light bg-light">
                <a class="navbar-brand" href="/">
                    <img src="../../assets/img/logo.png" alt="logo" class="logo-section">
                </a>
                <button class="navbar-toggler" :class="showMobileMenu ? 'collapsed' : ''" type="button"
                    data-toggle="collapse" data-target="#navbarSupportedContent" aria-controls="navbarSupportedContent"
                    :aria-expanded="showMobileMenu ? 'true' : 'false'" aria-label="Toggle navigation" @click="showMenu()">
                    <span class="navbar-toggler-icon"></span>
                </button>

                <div class="collapse navbar-collapse" id="navbarSupportedContent" :class="showMobileMenu ? 'show' : ''">
                    <ul class="navbar-nav mr-auto w-100 justify-content-end">
                        <li class="nav-item">
                            <a class="nav-link about-us-active"
                                :class="($route.path === '/') || ($route.path === '#about-us') ? 'active' : ''"
                                href="/#about-us">About us</a>
                        </li>
                        <li class="nav-item">
                            <a class="nav-link how-it-works-active" :class="$route.path === '#how-it-works' ? 'active' : ''"
                                href="/#how-it-works">How it works</a>
                        </li>
                        <li class="nav-item">
                            <a class="nav-link faq-active"
                                :class="($route.path === '#faq') || ($route.path === '/faq') ? 'active' : ''"
                                href="/#faq">FAQ</a>
                        </li>
                        <li class="nav-item">
                            <a class="nav-link contact-us-active" :class="$route.path === '/contact-us' ? 'active' : ''"
                                href="/contact-us" target="blank">Contact Us</a>
                        </li>
                        <li class="nav-item">
                            <a class="nav-link get-started-active" href="/register" target="blank">Sign up</a>
                        </li>
                    </ul>
                </div>
            </nav>
        </header>
    </div>
</template>

<script>
export default {
    data() {
        return {
            scrollActive: false,
            showMobileMenu: false,
        };
    },

    mounted() { },
    created() {
        window.addEventListener("scroll", this.handleScroll);
    },
    methods: {
        handleScroll() {
            this.scrollActive = window.scrollY > 250 ? true : false;
        },
        showMenu() {
            this.showMobileMenu = !this.showMobileMenu;
        },
    },
}
</script>

<style></style>